html,
body,
div,
p,
input,
ul,
li,
textarea {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
input {
  border-style: none;
}
input[type=text],
input[type=password],
input,
select,
textarea {
  padding: 10px 5px;
  border-radius: 4px;
}
input[type=checkbox] {
  width: auto;
}
ul {
  list-style: none;
}
.error {
  border: 1px solid red;
}
.graph {
  width: 400px;
  height: 400px;
  margin: 0 auto;
}
/* 前缀 */
.display-flex {
  display: flex;
}
